@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@900&family=Raleway:wght@400&display=swap');

/* Global Styles */
html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif; /* Apply Raleway Regular to body text */
  line-height: 1.6;
  color: #333;
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif; /* Apply Montserrat Black to headings */
  font-weight: 900;
}

p, li, span {
  font-family: 'Raleway', sans-serif; /* Apply Raleway to body text */
  font-weight: 400;
}

/* Section Styling */
section {
  padding: 100px 20px;
}

/* Add specific font sizes for headings */
h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

p {
  font-size: 1.2rem;
}

/* Button and general text styling */
button {
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}
/* Mobile Menu Overlay */
.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #64ffda; /* Bright teal overlay */
  z-index: 1000;
  display: none; /* Hidden by default */
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.mobile-menu-overlay.active {
  display: flex; /* Show the menu when active */
}

/* Menu Items */
.mobile-menu-overlay a {
  font-family: 'Raleway', sans-serif; /* Use Raleway Regular for the text */
  font-size: 2rem;
  color: #0a192f; /* Dark text color for contrast */
  text-decoration: none;
  margin: 20px 0;
  transition: color 0.3s ease;
}

.mobile-menu-overlay a:hover {
  color: #333; /* Slightly darker hover effect */
}
/* Hamburger Button (Menu Toggle) */
.hamburger {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1100; /* Ensure it's above the overlay */
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 20px;
}

/* Default state - teal color for the hamburger */
.hamburger div {
  width: 100%;
  height: 3px;
  background-color: #64ffda; /* Bright teal for the lines */
  transition: all 0.3s ease;
}

/* When active, change color to blue for the "X" */
.hamburger.active div {
  background-color: #0a192f; /* Blue color for the "X" */
}

/* Hamburger Active State Transformations */
.hamburger.active div:nth-child(1) {
  transform: translateY(8px) rotate(45deg); /* Rotate top line */
}

.hamburger.active div:nth-child(2) {
  opacity: 0; /* Hide middle line */
}

.hamburger.active div:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg); /* Rotate bottom line */
}

