/* Global Services Section */
.services {
    padding: 100px 20px;
    background-color: #f9f9f9;
  }
  
  .services h2 {
    font-size: 2.5rem;
    font-weight: 700;
    color: #0a192f;
    margin-bottom: 40px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    border-bottom: 3px solid #64ffda;
    display: inline-block;
    font-family: 'Raleway', sans-serif; /* Apply Raleway Regular for headings */
  }
  
  /* Service Cards Container */
  .service-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 40px;
  }
  
  /* Service Cards */
  .service-card {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    width: 250px;
    text-align: center;
    cursor: pointer;
    transition: transform 0.3s ease;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Vertically center the content */
    align-items: center; /* Center content horizontally */
    height: 200px; /* Set a fixed height for consistent sizing */
  }
  
  .service-card:hover {
    transform: translateY(-10px);
  }
  
  /* Service Titles */
  .service-card h3 {
    font-size: 1.5rem;
    margin-bottom: 20px;
    font-family: 'Raleway', sans-serif; /* Apply Raleway Regular for card titles */
    font-weight: 400;
  }
  
  /* Responsive Design for Smaller Screens */
  @media (max-width: 768px) {
    .services {
      padding: 80px 20px;
    }
  
    .service-cards {
      flex-direction: column;
      align-items: center;
    }
  
    .service-card {
      width: 90%;
      margin-bottom: 20px;
      height: auto; /* Remove fixed height on smaller screens */
    }
  }
  