/* Overlay Background */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure it sits on top */
  }
  
  /* Overlay Content */
  .overlay-content {
    background-color: white;
    padding: 40px;
    border-radius: 10px;
    max-width: 80%; /* Max width of the content for larger screens */
    width: 600px;   /* Base width */
    text-align: center;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.25); /* Shadow for depth */
  }
  
  /* Adjust for desktop */
  @media (min-width: 1024px) {
    .overlay-content {
      width: 70%;    /* Wider on desktop */
      max-width: 800px;  /* Cap the max width at 800px */
    }
  }
  
  /* Adjust for mobile */
  @media (max-width: 768px) {
    .overlay-content {
      width: 90%;    /* Nearly full width on mobile */
      padding: 20px; /* Smaller padding for mobile */
    }
  }
  
  /* Headings and Text */
  .overlay-content h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .overlay-content p {
    font-size: 1.2rem;
    margin-bottom: 30px;
  }
  
  /* Close Button */
  .close-btn {
    padding: 10px 20px;
    background-color: #64ffda;
    border: none;
    color: #0a192f;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
  }
  
  .close-btn:hover {
    background-color: #52d1b1;
  }
  /* Benefits List */
.benefits-list {
    text-align: left;
    margin-top: 20px;
    list-style-type: disc;
    padding-left: 20px; /* Reduced padding to bring bullets closer to text */
    font-size: 1.1rem;
  }
  
  .benefits-list li {
    margin-bottom: 10px;
    padding-left: 0;  /* Ensure no extra padding is applied */
  }
  
  @media (max-width: 768px) {
    .benefits-list {
      padding-left: 15px; /* Smaller padding for mobile */
    }
  }
  